import React from 'react';
import { Switch, Route } from 'react-router-dom';
import List from "./components/landingpage"
import ParagonConnect from "./components/paragonConnect"


function App() {
    return (
        <Switch>
            <Route path="/" component={List} />
            <Route exact path="/paragonConnect" component={ParagonConnect} />
        </Switch>
    );
}

export default App;